<template>
  <div class="hello">
    <div class="container">
      <div class="container-head">
        <h3 class="title">Todo List MEVN Stack</h3>
        <hr />
      </div>
      <div class="container-form">
        <h2>Add Your To Do Below...</h2>
        <input @keyup.enter="submitPost" type="text" v-model="text" />
      </div>
      <div class="container-alert">
        <h3 class="error" v-if="error">{{ error }}</h3>
        <h3 class="loading" v-if="loading">{{ loading }}</h3>
      </div>
      <div v-if="posts != []" class="container-post">
        <div
          v-for="(post,index) in posts"
          @dblclick="deletePost(post._id)"
          :key="index"
          class="post-wrapper"
        >
          <p
            class="post-date"
          >{{ `${post.createdAt.getDate() }/${post.createdAt.getMonth()+1 }/${post.createdAt.getFullYear() }`}}</p>
          <h1 class="post-title">{{ post.text}}</h1>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import PostService from "../Post";
export default {
  name: "Post",
  data() {
    return {
      posts: [],
      text: "",
      error: "",
      loading: ""
    };
  },
  async created() {
    try {
      const log = await PostService.getPost();
      this.checkError(log);
      this.posts = log.data;
    } catch (err) {
      return;
    }
  },
  methods: {
    async submitPost() {
      try {
        this.loading = "Adding Post...";
        const res = await PostService.insertPost(this.text);
        this.text = " ";
        const data = await PostService.getPost();
        this.posts = data.data;
        this.loading = " ";
        this.checkError(res);
      } catch (error) {
        return;
      }
    },
    async deletePost(id) {
      try {
        this.loading = "Deleting Post...";
        const res = await PostService.deletePost(id);
        this.text = "";
        const data = await PostService.getPost();
        this.posts = data.data;
        this.loading = "";
        this.error = this.checkError(res);
      } catch (error) {
        return;
      }
    },
    checkError(request) {
      if (request.error) {
        this.error = request.error;
      } else {
        return;
      }
    }
  }
};
</script>

